import { __decorate } from "tslib";
import { Vue, Component, Mixins } from 'vue-property-decorator';
import { newsMapper } from '@/modules/news/module/store';
import MsgBoxToolsApp from '@/mixins/MsgBoxToolsApp';
import MsgBoxTools from '@/modules/news/mixins/MsgBoxTools';
import MessageBox from '@/components/MessageBox.vue';
import { authMapper } from '@/modules/auth/module/store';
const NewsMappers = Vue.extend({
    computed: {
        ...newsMapper.mapState(['list']),
        ...newsMapper.mapGetters(['isLoading'])
    },
    methods: {
        ...newsMapper.mapActions(['getList']),
    }
});
const AuthMappers = Vue.extend({
    computed: {
        ...authMapper.mapGetters(['activeMenuSectionByLink'])
    }
});
let ModuleNews = class ModuleNews extends Mixins(NewsMappers, AuthMappers, MsgBoxToolsApp, MsgBoxTools) {
    constructor() {
        super(...arguments);
        this.secondBtn = { type: 'success', isPlain: true };
    }
    get fetchListFailed() { return this.requestStatus === 'failFetchList'; }
    get link() { return this.$route && this.$route.matched && this.$route.matched[0].path.slice(1); }
    get activeSection() { return this.link && this.activeMenuSectionByLink(this.link); }
    created() {
        this.updateList();
    }
    goToPageApp() { this.$router.push({ path: '/' }); }
    onFirstBtnClick() {
        if (this.msgBoxIsShown)
            this.closeMsgBox();
        switch (this.requestStatus) {
            case 'failFetchList':
                this.updateList();
                break;
        }
    }
    updateList() {
        if (this.list.isLoading)
            return;
        this.getList()
            .catch((err) => {
            if (err && err.status && err.status.toString().slice(0, 2) == 40)
                this.$emit('goToPageAuth');
            else {
                this.requestStatus = 'failFetchList';
                this.openMsgBox();
            }
        });
    }
};
ModuleNews = __decorate([
    Component({
        components: {
            MessageBox
        }
    })
], ModuleNews);
export default ModuleNews;
